
interface listener {
    subject: string;
    callback: ( ...content: any[] ) => any;
    options: {
        once?: boolean
    }
}

function _MessagePort ( port: MessagePort ) {
    
    const listeners: listener[] = [];
    const __port = port;
    const __listeners = listeners;

    port.addEventListener( 'message', function ( event: MessageEvent ) {
        const { subject: messageSubject, content }: { subject: string, content: any[] } = event.data;
        listeners.forEach( function ({ subject: listeningSubject, callback, options }, index ): void {
            if ( messageSubject !== listeningSubject ) return ;
            callback( ...content );

            if ( options.once ) {
                return void delete listeners[ index ];
            }
        })
    })

    function addListener ( subject: string, callback: ( ...content: any[] ) => any, options?: { once?: boolean }): void {
        return void __listeners.push( {
            subject, callback,
            options: {
                once: false,
                ... options
            }
        });
    }


    function removeListener ( subject: string ): void ;
    function removeListener ( subject: string, callback: ( ...content: any[] ) => any ): void;
    function removeListener ( subject: string, targetCallback?: ( ...content: any[] ) => any ): void {
        __listeners.forEach( function ({ subject: listeningSubject, callback }, index ): void {
            if ( subject !== listeningSubject ) return ;
            
            if ( targetCallback === callback || targetCallback === undefined ) {
                delete __listeners[ index ];
            }
        });
    }

        
    function postMessage ( subject: string, ...content: any[] ): void {
        return __port.postMessage( {
            subject, content
        });
    }

    return {
        addListener,
        removeListener,
        postMessage
    }
}

export { _MessagePort as MessagePort }