import { contextBridge, ipcRenderer, shell } from "./electron";
import { MessagePort } from "./message-manager";

const { port1: mainPort, port2: rendererPort } = new MessageChannel();

ipcRenderer.postMessage( 'initial', null, [ mainPort ]);
rendererPort.start();

const port = MessagePort( rendererPort );

contextBridge.exposeInMainWorld( 'port', port );
contextBridge.exposeInMainWorld( 'shell', shell );

port.postMessage('test')